.container-support {
  width: 100%;
  height: 100%;
}
.navbar-support {
  width: 90%;
  height: 60px;
  background: linear-gradient(45deg, rgb(153, 218, 67), rgb(114, 226, 33));
  border-radius: 10px;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  margin: auto;
  z-index: 100;
  align-items: center;
  color: white;
}
.menulist-support {
  max-width: 800px;
  height: 40px;
  background-color: rgb(203, 203, 203);
  border-radius: 10px;
  display: flex;
  margin: auto;
  margin-top: 100px;
}

.menulist-support > div {
  width: 33.33%;
  height: 100%;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: 500ms ease;
}
/*--------------- ticket -----------------*/

.container-ticket {
