/*--------------- newticket -----------------*/

.container-newticket {
  width: 100%;
  height: 100%;
}
.navbar-newticket {
  width: 90%;
  height: 60px;
  background: linear-gradient(45deg, rgb(153, 218, 67), rgb(114, 226, 33));
  border-radius: 10px;
  display: flex;
  justify-content: space-around;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  margin: auto;
  z-index: 100;
  align-items: center;
  color: white;
}
.navbar-newticket > a > div {
  cursor: pointer;
}
.navbar-newticket > a > div > svg {
  font-size: 20px;
}

.content-newticket {
  margin-bottom: 30px;
}
.content-newticket > div {
  display: flex;
  flex-direction: column;
  max-width: 600px;
  height: auto;
  margin: auto;
  margin-top: 100px;
}
.text-newticket {
  text-align: center;
}
.selectrow-newticket {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 10px;
}
.selectrow-newticket > div {
  width: 40%;
  height: 40px;
  background-color: rgba(204, 204, 204, 0.888);
  border-radius: 20px;
  margin: 0 5px;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.selectrow-newticket > div > div {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  border: 1px solid gray;
  margin: 0 10px;
  overflow: hidden;
}
.selectrow-newticket > div > div > div {
  width: 100%;
  height: 100%;
  background-color: green;
  opacity: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.selectrow-newticket > div > div > div > svg {
  color: white;
}
.selectrow-newticket > div > p {
  color: rgb(78, 78, 78);
  font-size: 13px;
}
.titleinpue-newticket {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 40px;
  position: relative;
}
.titleinpue-newticket > p {
  position: absolute;
  font-size: 13px;
  color: gray;
  right: 50px;
  top: 15px;
  z-index: 100;
  background-color: white;
  width: 80px;
  transition: 300ms ease;
}
.titleinpue-newticket > input {
  width: 85%;
  height: 40px;
  border-radius: 10px;
  outline: unset;
  border: 1px solid;
  padding: 5px 15px;
  font-family: "dana";
}
.discriptioninput-newticket {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 20px;
  position: relative;
}
.discriptioninput-newticket > p {
  position: absolute;
  font-size: 13px;
  color: gray;
  right: 50px;
  top: 15px;
  z-index: 100;
  background-color: white;
  max-width: 250px;
  min-width: 100px;
  transition: 300ms ease;
}
.discriptioninput-newticket > textarea {
  width: 85%;
  border-radius: 10px;
  outline: unset;
  border: 1px solid;
  padding: 15px 15px;
  font-family: "dana";
  resize: vertical;
}
.selectfile-newticket {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 30px;
}
.selectfile-newticket > div {
  margin: 0 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.selectfile-newticket > div > div {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #4ba900;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  cursor: pointer;
}
.selectfile-newticket > div > div > svg {
  color: white;
  font-size: 20px;
}
.selectfile-newticket > div > p {
  font-size: 10px;
  color: gray;
}

.content-newticket > div > button {
  transition: 500ms ease;
  width: 280px;
  height: 35px;
  border: unset;
  outline: unset;
  border-radius: 20px;
  /* background: -webkit-gradient(linear,right top,left top,color-stop(40%,#f8910d),to(#ffdea7)); */
  background: -webkit-gradient(
    linear,
    right top,
    left top,
    color-stop(40%, #79e027),
    to(#c3deb9)
  );
  font-family: "Dana";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: bold;
  cursor: pointer;
  color: white;
  margin: auto;
  margin-top: 20px;
}

 /* ------------------------ med ------------------*/
 @media (max-width: 555px) {
  .selectfile-newticket > div
  {
    margin: 0 40px;
  }
 }
 @media (max-width: 453px) {
  .selectrow-newticket > div > p
  {
    font-size: 10px;
  }
  .selectrow-newticket > div > div
  {
    width: 20px;
    height: 20px;
    margin: 0 4px;
  }
 }
 @media (max-width: 300px) {
  .titleinpue-newticket > p
  {
    right: 10px;
  }
  .discriptioninput-newticket > p
  {
    right: 20px;
  }
  .content-newticket > div > button
  {
    width: 230px;
  }
 }

