.switch {
  position: relative;
  display: inline-block;
  width: 55px;
  height: 30px;
  margin: 0 10px;

}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
    content: "";
    height: 24.5px;
    width: 24.5px;
    left: 4px;
    bottom: 3px;
    background-color: white;
    transition: 0.4s;
}
input:checked + .icon::before {
  font-family: "FontAwesome";
  content: "\f00c";
  transform: rotate(45deg);
  padding-top: 5px;
}
.toggle {
  font-family: "FontAwesome";
  font-style: normal !important;
}
.dev-footer {
  margin-top: 71px;
  color: #4e4a46;
}
input + .icon::before {
  font-family: "FontAwesome";
  content: "\f00d";
  padding-top: 5px;
}
.slider.w-color:before {
  background-color: #a37eba;
}
.slider.w-color {
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
input:checked + .slider.w-color {
  background-color: #ccc;
}
input:checked + .slider.icon {
  background-color: #a37eba;
}

.slider.icon:before {
  background-color: #ffe05d;
}
.slider.icon:after {
  background-color: #e2df23;
}
input:checked + .slider {
  background-color: #2196f3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(23px);
  -ms-transform: translateX(23px);
  transform: translateX(23px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
.disabled-toggle {
  opacity: 0.5;
}
.disabled-toggle .switch span.slider{
  cursor: not-allowed ;
}
