.container-store
{

    width: 100%;
    height: 100%;
   
}
.navbar-store
{
    width: 90%;
    height: 60px;
    background: linear-gradient(45deg, rgb(153, 218, 67), rgb(114, 226, 33));
    border-radius: 10px;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    margin: auto;
    z-index: 100;
    align-items: center;
    color: white;
}
.content-store
{
    width: 100%;
    height: auto;
    
  
}
.content-store > div
{
    
    margin: auto;
}
.row1-content-store
{
   
   max-width: 1000px;
    height: 200px;
    margin: auto;
    margin-top: 100px;
    direction: rtl;
   
   
   
}
.itemslider-row1-content-store
{
    width: 290px;
    height: 180px;
    background-color: green;
    border-radius: 18px;
    margin: auto;
    overflow: hidden;
}
.itemslider-row1-content-store > img
{
    width: 100%;
    height: 100%;
}

.rowtitle-content-store
{
    max-width: 1000px;
    height: 55px;
    margin: auto;
    margin-top: 50px;
    direction: rtl;
    border-radius: 15px;
    background-color: rgb(236 236 236);
    display: flex;
    justify-content:space-between;
    align-items: center;

}
.rowtitle-content-store > div
{
    margin: 0 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.rowtitle-content-store :first-child > svg
{
    font-size: 25px;
    color: #5aa91c;
}
.rowtitle-content-store :first-child > p
{
    font-size: 13px;
    margin-right: 10px;
}
.rowtitle-content-store :last-child > svg
{
    font-size: 12px;
    color: #5aa91c;
}
.rowtitle-content-store :last-child :nth-child(2)
{
    color: #59a91c82;
}

.rowtitle-content-store :last-child > p
{
    font-size: 13px;
    margin-left: 10px;
    color: #4a8d17;
}







.rowtitle2-content-store
{
    max-width: 1000px;
   
    margin: auto;
    margin-top: 50px;
    direction: rtl;
    border-radius: 15px;
   
    display: flex;
    justify-content:space-between;
    align-items: center;

}
.rowtitle2-content-store > div
{
    margin: 0 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.rowtitle2-content-store :first-child > p
{
    font-size: 18px;
    margin-right: 10px;
}



.rowtitle2-content-store :last-child > p
{
    font-size: 13px;
    margin-left: 10px;
    color: #4a8d17;
}



.row2-content-store
{
   
   max-width: 1000px;
    height: 250px;
    margin: auto;
    margin-top: 10px;
    direction: rtl;
    margin-bottom: 40px;
   
   
   
}
.itemslider-row2-content-store
{
    width: 200px;
    height: 250px;
    background-color: rgb(236, 236, 236);
    border-radius: 15px;
    margin: auto;
    display: flex;
    flex-direction: column;
   
}
.img-itemslider-row2-content-store 
{
    width: 90%;
    height: 90px;
    border-radius: 15px;
    overflow: hidden;
    margin: 8px auto;
  
   
}
.img-itemslider-row2-content-store  > img
{
    width: 100%;
    height: 100%;
}
.title-itemslider-row2-content-store
{
    width: 88%;
    padding: 5px 5px;
    height: 70px;
    margin: auto;
    margin-top: 0;
    border-bottom: 1px solid rgb(180, 180, 180);
    margin-bottom: 10px;
}
.title-itemslider-row2-content-store > p
{
    text-align: right;
}
.rowpay-itemslider-row2-content-store
{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.rowpay-itemslider-row2-content-store > div
{
    margin: 0 15px;
}
.rowpay-itemslider-row2-content-store :nth-child(2){
    width: 35px;
    height: 40px;
    border-radius: 10px;
    border: 1px solid green;
    display: flex;
    justify-content: center;
    align-items: center;
}
.rowpay-itemslider-row2-content-store :nth-child(2) > svg
{
    color: green;
}
.adsbox-content-store
{
    margin: auto;
    max-width: 500px;
    height: 200px;
    border-radius: 15px;
    overflow: hidden;
    margin-bottom: 40px;
}
.adsbox-content-store > img
{
    width: 100%;
    height: 100%;
}

.adsbox2-content-store
{
    margin: auto;
    max-width: 500px;
    height: 200px;
    border-radius: 15px;
    overflow: hidden;
    margin-bottom: 100px;
}
.adsbox2-content-store > img
{
    width: 100%;
    height: 100%;
}


/*--------------------------- product -------------------------------*/

.content-product
{
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 100px;
}
.content-product > :nth-child(1)
{
    width: 500px;
    height: auto;
    
}
.content-product > :nth-child(1) > :nth-child(1)
{
    width: 100%;
    height: 400px;
    border-radius: 10px;
    background-color: rgb(200 200 200 / 18%);
    padding: 10px 0;
}
.img-product
{
    width: 95%;
    height: 250px;
    border-radius: 10px;
    margin: auto;
    overflow: hidden;
}
.img-product > img
{
    width: 100%;
    height: 100%;
}
.title-product
{
    width: 95%;
    height: 50px;
    border-bottom: 1px solid gainsboro;
    margin: 20px auto;
}
.title-product > p
{
    text-align: right;
    color: black;
    font-size: 14px;
    font-weight: bold;
}
.total-product
{
    width: 95%;
    display: flex;
    margin: 10px auto;
    align-items: center;
}
.total-product > :nth-child(1)
{
    flex-grow: 1;
    text-align: right;
    font-size: 13px;
}
.total-product > :nth-child(2) > button
{
    width: 150px;
    height: 45px;
    border-radius: 10px;
    background-color: unset;
    border: 1px solid  rgb(87, 181, 19);
    outline: unset;
    font-family: 'dana';
    color:  rgb(87, 181, 19);
    cursor: pointer;
}
.content-product > :nth-child(1) > :nth-child(2)
{
    width: 100%;
    height: 400px;
    border-radius: 10px;
    background-color: rgb(200 200 200 / 18%);
    padding: 10px 0;
    margin-top: 20px;
}
.discriptions-product
{
    width: 95%;
    text-align: right;
    margin: auto;
    font-size: 13px;
    color: rgb(85, 85, 85);
}
.box-part-product
{
    width: 95%;
    margin: 10px auto;
    margin-top: 70px;

}
.box-part-product > div
{
    width: 100%;
    height: 50px;
    border-radius: 10px;
    background-color: #c3c3c375;
    margin: 10px 0;
    display: flex;
    align-items: center;
}
.box-part-product > div > :nth-child(1)
{
    flex-grow: 1;
    margin-right: 10px;
}
.box-part-product > div > :nth-child(1) > :nth-child(1)
{
    text-align: right;
    font-size: 13px;
    font-weight: bold;
}
.box-part-product > div > :nth-child(1) > :nth-child(2)
{
    display: flex;
    font-size: 13px;
    color: rgb(138, 138, 138);
}
.box-part-product > div > :nth-child(2)
{
    margin-left: 10px;
    display: flex;
}
.box-part-product > div > :nth-child(2) >div
{
    width: 30px;
    height: 30px;
    border-radius: 10px;
    border: 1px solid rgb(87, 181, 19);
    margin: 0 5px;
}