.container-status {
    width: 100%;
    height: 100%;
  }
  .navbar-status {
    width: 90%;
    height: 60px;
    background: linear-gradient(45deg, rgb(153, 218, 67), rgb(114, 226, 33));
    border-radius: 10px;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    margin: auto;
    z-index: 100;
    align-items: center;
    color: white;
  }
  .content-status 
  {
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-bottom: 100px;
  }
  


  .box1-content-status
  {
    max-width: 1000px;
    min-height: 280px;
    padding-bottom: 10px;
    margin: auto;
    margin-top: 85px;
    background-color: rgb(223, 223, 223);
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    
  }
  .row1-box1-content-status
  {
    display: flex;
    justify-content: center;
    margin-top: 35px;
    flex-wrap: wrap;
  }
  
  .row1-box1-content-status > div
  {
    width: 450px;
    height: 45px;
    background-color: #fbfbfb;
    border-radius: 20px;
    margin: 10px 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 10px;

  }
  .row1-box1-content-status > div > div
  {
    display: flex;
   
  }
  .row1-box1-content-status > div > p
  {
    display: flex;
    flex-grow: 1;
    font-size: 13px;
    color: #8e8e8e;
  }

  .row1-box1-content-status > div > div > p
  {
    font-size: 13px;
    color: #8e8e8e;
  }
  .row1-box1-content-status > div > div > svg
  {
    font-size: 13px;
    color: #8e8e8e;
    margin-left: 5px;
  }


  .box2-content-status
  {
    max-width: 1000px;
    min-height: 65px;
    margin: auto;
    margin-top: 50px;
    background-color: rgb(223, 223, 223);
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    
  }
  .box2-content-status > p
  {
    margin-top: 10px;
    color: #8e8e8e;
  }

  /* ------------------------ med ------------------*/
@media (max-width: 1170px) {
  .box1-content-status
  {
    max-width: 700px !important;
  }
  .box2-content-status
  {
    max-width: 700px !important;
  }
}
@media (max-width: 860px) {
  .box1-content-status
  {
    max-width: 500px !important;
  }
  .box2-content-status
  {
    max-width: 500px !important;
  }
}

 