.container-yourterms
{

    width: 100%;
    height: 100%;
    padding: 20px 0;
   
}

.navbar-yourterms
{
    max-width: 600px;
    height: 50px;
    background: linear-gradient(45deg, rgb(153, 218, 67), rgb(114, 226, 33));
    margin-left: 50px;
    border-radius: 50px 0 0 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.navbar-yourterms > p
{
    color: white;
    flex-grow: 1;
}
.navbar-yourterms > div
{
    height: 38px;
    width: 40px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    margin-left: 10px;
}

.content-yourterms > :nth-child(1)
{
    max-width: 1000px;
    height: auto;
    background-color: #f0f0f0;
    border-radius: 150px 0 0 150px;
    margin: auto;
    padding: 25px 30px;
    margin-top: 10px;
    
}
.row-yourterms
{
    width: 100%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}
.row-yourterms > :nth-child(1)
{
    margin-top: 20px;
    
}
.row-yourterms > :nth-child(2)
{
    margin-top: 20px;
}
.row-yourterms > div
{
    width: 400px;
    height: 70px;
    border-radius: 20px;
    background-color: rgb(255, 255, 255);
    margin: 10px 0;
    border: 1px solid rgb(45, 218, 245);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.row-yourterms > div > p
{
    margin: 5px 5px;
}
.row-yourterms > div > :nth-child(2)
{
    color: gray;
}
.proposal-yourterms
{
    margin-right: 50px;
    margin-top: 10px;
}
.proposal-yourterms > p
{
    text-align: right;
}

.tip-yourterms
{
    margin-right: 50px;
    margin-top: 10px;
    

}
.tip-yourterms > p
{
    color: red;
    text-align: right;
}

/* ------------------------ med ------------------*/

@media (max-width: 504px) {
    .row-yourterms > div {
      width: 240px;
    }

}
/*----------------------------DailyActivity-------------------------------*/

.content-dailyactivity > :nth-child(2)
{
    max-width: 1000px;
    height: auto;
    background-color: #f0f0f0;
    border-radius: 150px 0 0 150px;
    margin: auto;
    padding: 25px 30px;
    margin-top: 50px;
    
}
.content-dailyactivity > :nth-child(2) > div
{
    width: 50%;
    min-height: 45px;
    border-radius: 15px;
    background-color: rgb(255, 255, 255);
    margin: 20px auto;
    display: flex;
    align-items: center;
    cursor: pointer;
   
    
}
.content-dailyactivity > :nth-child(2) > div > p
{
    margin: 0 20px;
}
.content-dailyactivity > :nth-child(2) > div > div
{
    min-width: 25px;
    height: 25px;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #eaeaea;
   margin: 0 10px;
}
.content-dailyactivity > :nth-child(2) > div > div > svg
{
    color: #20afc5;
}

.content-dailyactivity > :nth-child(2) > div:hover
{
    border: 1px solid #20afc5;
}
.content-dailyactivity > :first-child
{
    position: relative;
    max-width: 645px;
    min-height: 50px;
    padding: 10px 10px;
    background-color: #94e7e0;
    top: 44px;
    margin: auto;
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 500ms ease;
    opacity:0;
}
.content-dailyactivity > :first-child > svg
{
    position: absolute;
    bottom: -16px;
    color: #94e7e0;
    font-size: 30px;
    left: 55px;
}
/* ------------------------ med ------------------*/

@media (max-width: 504px) {
    .content-dailyactivity > :nth-child(2) > div {
      width: 85%;
    }

}
