
.container-login
{
    width: 100%;
    height: 500px;
    background: linear-gradient(45deg, rgb(229, 255, 196), rgb(137, 240, 147));
    overflow: scroll;
}
.title-box-login
{
    margin: auto;
    margin-top: 20px;
    max-width: 320px;
    height: 0;
    border-left: 199px solid rgb(0 197 19 / 41%);
    border-right: 199px solid rgb(0 197 19 / 41%);
    border-bottom: 206px solid #ffffff;
    border-radius: 10px;

}
.title-box-login :nth-child(2)
{
    font-weight: bold;
    font-size: 30px;
    
}
.bottom-box-login
{
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    background-color: white;
    height: 450px;
    border-radius: 60px 60px 0 0;
}

.content-bottom-login
{
    margin: auto;
    margin-top: 30px;
    max-width: 700px;
    height: 400px;
  
    border-bottom: 2px solid rgb(255, 195, 0);
    border-left: 2px solid rgb(255, 195, 0);
    border-right: 2px solid rgb(255, 195, 0);
    border-radius: 20px;

}
.content-bottom-login > :nth-child(1)
{
    font-weight: bold;
    font-size: 20px;
}
.input-box-verifi
{
   
    display: flex;
    justify-content: center;
    margin: auto;
    margin-top: 35px;
    height: 140px;
    gap: 2rem;
    direction: ltr;
}

.input-box-login > p
{
    position: absolute;
    z-index: 100;
    font-size: 14px;
    color: rgb(130, 130, 130);
    top: 38px;
    left: 95px;
    background-color: white;
    width: 115px;
    transition: 100ms ease;
}
 .input-box-verifi > div > input
{
       width: 40px;
    height: 50px;
    border-radius: 10px;
    text-align: center;
} 
.inpute-verifi
{
    width: 100px !important;
}
.submit-box-login
{
    display: flex;
    justify-content: center;
    
}

.submit-box-login > button{
    transition: 500ms ease;
    width: 300px;
    height: 55px;
    border: unset;
    outline: unset;
    border-radius: 20px;
    /* background: -webkit-gradient(linear,right top,left top,color-stop(40%,#f8910d),to(#ffdea7)); */
    background: -webkit-gradient(linear,right top,left top,color-stop(40%,#aaf6a4),to(#f0f0f0));
   font-family: 'Dana';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: bold;
  cursor: pointer;
  

}
.submit-box-login > button:hover
{
    transition: 500ms ease;
     /* background: -webkit-gradient(linear,right top,left top,color-stop(40%,#f8910d),to(#ffeccb));  */
     width: 320px;
     
}
.input-box-login > :nth-child(3){
    position: absolute;
    bottom: 20px;
    width: 115px;
    height: 30px;
    background-color: #ff541b;
    border-radius: 8px;
    left: -50px;
    opacity: 0;
    transition: 500ms ease;
    display: flex;
    justify-content: center;
}

.input-box-login > :nth-child(3) > p
{
    font-size: 12px;
    color: white;
    margin: auto;
}

.input-box-login > :nth-child(3) > svg{
    transform: rotate(180deg);
    color: #ff541b;
    top: -9px;
    position: absolute;
    left: 20px;
}

.timerbox-verifi
{
    width: 280px;
    height: 60px;
    border-radius: 5px;
    display: flex;
    margin: auto;
    justify-content: center;
    background-color: #f3f3f3;
    gap: 5rem;
    margin-top: 20px;
}
.timerbox-verifi > p
{
    margin-top: 20px;
}
.Toastify__toast-body > div:last-child {
    font-family: 'Dana';
}
.title-box-login2
{
    display: none;
    margin: auto;
    margin-top: 20px;
}
.title-box-login2 :nth-child(2)
{
    font-weight: bold;
    font-size: 30px;
    
}
/* ------------------------ med ------------------*/

@media (max-width: 783px) {
    .title-box-login {
      border-bottom: 290px solid #ffffff;
    }
    

}

@media (max-width: 589px) {
    .title-box-login {
     display: none;
    }
    .title-box-login2
    {
        display: block;
    }

}