
.container-login
{
    width: 100%;
    height: 500px;
    background: linear-gradient(45deg, rgb(229, 255, 196), rgb(137, 240, 147));
    overflow: hidden;
}
.title-box-login
{
    margin: auto;
    margin-top: 20px;

}
.title-box-login :nth-child(2)
{
    font-weight: bold;
    font-size: 25px;
    
}
.bottom-box-login
{
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    background-color: white;
    height: 450px;
    border-radius: 60px 60px 0 0;
}

.content-bottom-login
{
    margin: auto;
    margin-top: 30px;

}
.content-bottom-login > :nth-child(1)
{
    font-weight: bold;
    font-size: 20px;
}
.input-box-login 
{
    position: relative;
    width: 200px;
    display: flex;
    justify-content: center;
    margin: auto;
    margin-top: 35px;
    height: 140px;
}
.input-box-login  > :nth-child(4)
{
    position: absolute;
    width: 90px;
    height: 50px;
    border-radius: 10px;
    left: -40px;
    background-color: #e4e4e4;
    top: 27px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
.input-box-login  > :nth-child(4) > div
{
    display: flex;
    justify-content: center;
    align-items: center;
}
.input-box-login  > :nth-child(4) > div >  :nth-child(1) > svg
{
    font-size: 14px;
    
}
.input-box-login  > :nth-child(4) > div > :nth-child(2)
{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 10px;
}
.input-box-login  > :nth-child(4) > div > :nth-child(2) > p
{
   margin-left: 5px;
}
.input-box-login  > :nth-child(5)
{
    z-index: 100;
    position: absolute;
    width: 200px;
    background-color: #e4e4e4;
    left: -45px;
    top: 84px;
    overflow-y: scroll;
    height: 160px;
    direction: rtl;
    border-radius: 10px;
}
.input-box-login  > :nth-child(5) > ul
{
    margin: 0;
    padding: 0;
}
.input-box-login  > :nth-child(5) > ul > li
{
    display: flex;
    justify-content: space-around;
    margin: 10px 0;
    cursor: pointer;
}

.input-box-login  > :nth-child(5)::-webkit-scrollbar {
    background-color: #fff;
    width: 10px;
    border-radius: 15px;
   
}

/* background of the scrollbar except button or resizer */
.input-box-login  > :nth-child(5)::-webkit-scrollbar-track {
    background-color: #66339966;
    border-radius: 10px;
   
}
.input-box-login  > :nth-child(5)::-webkit-scrollbar-track:hover {
    background-color: #f4f4f4;
    border-radius: 15px;
}

/* scrollbar itself */
.input-box-login  > :nth-child(5)::-webkit-scrollbar-thumb {
    background-color: #fff;
    border-radius: 16px;
    border: 5px solid #fff;
    
}
.input-box-login  > :nth-child(5)::-webkit-scrollbar-thumb:hover {
    background-color: #a0a0a5;
    border: 4px solid #f4f4f4;
}

.input-box-login > p
{
    position: absolute;
    z-index: 100;
    font-size: 14px;
    color: rgb(130, 130, 130);
    top: 38px;
    left: 95px;
    background-color: white;
    width: 115px;
    transition: 100ms ease;
}
.input-box-login > input
{
    outline: unset;
    border-radius: 10px;
    border: 2px solid #d9d9d6;
    height: 50px;
    text-align: left;
    /* padding: 5px 20px; */
    width: 180px;
    margin-top: 20px;
    position: absolute;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 100px;
    padding-right: 10px;
}
.submit-box-login
{
    display: flex;
    justify-content: center;
    position: relative;
    width: 300px;
    margin: auto;
    
}

.submit-box-login > button{
    transition: 500ms ease;
    width: 300px;
    height: 55px;
    border: unset;
    outline: unset;
    border-radius: 20px;
    /* background: -webkit-gradient(linear,right top,left top,color-stop(40%,#f8910d),to(#ffdea7)); */
    background: -webkit-gradient(linear,right top,left top,color-stop(40%,#aaf6a4),to(#f0f0f0));
   font-family: 'Dana';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: bold;
  cursor: pointer;
  

}
.submit-box-login > button:hover
{
    transition: 500ms ease;
     /* background: -webkit-gradient(linear,right top,left top,color-stop(40%,#f8910d),to(#ffeccb));  */
     width: 320px;
     
}
.input-box-login > :nth-child(3){
    position: absolute;
    bottom: 20px;
    width: 115px;
    height: 30px;
    background-color: #ff541b;
    border-radius: 8px;
    left: -50px;
    opacity: 0;
    transition: 500ms ease;
    display: flex;
    justify-content: center;
}

.input-box-login > :nth-child(3) > p
{
    font-size: 12px;
    color: white;
    margin: auto;
}

.input-box-login > :nth-child(3) > svg{
    transform: rotate(180deg);
    color: #ff541b;
    top: -9px;
    position: absolute;
    left: 20px;
}




  .submit-box-login > span
  {
    
    position: absolute;
    left: 50px;
    top: 8px;

  }