.container-profile
{

    width: 100%;
    height: 100%;
   
}
.navbar-profile
{
    width: 90%;
    height: 80px;
    background: linear-gradient(45deg, rgb(153, 218, 67), rgb(114, 226, 33));
    display: flex;
    justify-content: center;
    margin: auto;
    padding: 0 15px;
    border-radius: 10px;
}

.navbar-profile > div
{
    width: 800px;
    height: 50px;
   
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
    direction: rtl;
    
}
.profilebox
{
    display: flex;
    flex-grow: 1;
    
}
.profilebox > div
{
    width: 75px;
    height: 70px;
    border-radius: 10px;
    background-color: rgb(255 255 252);
    overflow: hidden;
}
.profilebox > div > img
{
    width: 100%;
    height: 100%;

}
.settingbox-profile > div
{
    width: 40px;
    height: 40px;
    border-radius: 10px;
    background-color: rgb(244 244 245 / 65%);
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
.settingbox-profile > div > a 
{
    display: flex;
    justify-content: center;
    align-items: center;
}
.settingbox-profile > div > a > svg{
    color: #615b5b;
}
.content-profile
{
    max-width: 1200px;
    height: auto;
    border-radius: 20px;
    background-color: #e6e6e6;
    margin: auto;
    margin-top: 10px;
    display: flex;
    flex-direction: column;
   padding: 10px 10px;
   z-index: -100;
   justify-content: center;
   margin-bottom: 100px;
}
.content-profile > :nth-child(1)
{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.info-user-profile
{
    width: 500px;
    height: 255px;
    border-radius: 30px;
    background-color: white;
    position: relative;
    margin: auto;
    margin-top: 40px;
    
}
.icon-info-user-profile
{
    width: 45px;
    height: 45px;
    border-radius: 50%;
    background-color: rgb(255, 255, 255);
    top: -21px;
    position: absolute;
    left: 45.5%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.icon-info-user-profile > div
{
    width: 36px;
    height: 36px;
    border-radius: 50%;
    background-color: #e4e4e4;
    display: flex;
    justify-content: center;
    align-items: center;
}
.icon-info-user-profile > div > svg{
    color:#b6b6b6
}
.row1-info-user-profile
{
    width: 90%;
    height: 20px;
    border-bottom: 0.5px solid #e1e1e1;
    display: flex;
    margin-top: 20px !important;
    margin: auto;
}
.row1-info-user-profile > :nth-child(1)
{
    display: flex;
    flex-grow: 1;
    font-size: 13px;
    color: gray;
}
.row1-info-user-profile > :nth-child(2)
{
  
    font-size: 15px;
    color: rgb(0, 0, 0);
}
.edit-button-info-user-profile
{
    width: 75px;
    height: 30px;
    border-radius: 15px;
    background-color: #e4e4e4;
    border: unset;
    outline: unset;
    font-family: 'Dana';
    margin-right: 370px;
    margin-top: 20px;
    color: #0bab04;
    cursor: pointer;
    transition: 500ms ease;
}
.edit-button-info-user-profile:hover
{
    background-color: #bebebe;
    transition: 500ms ease;
}
.info-user-profile > :nth-child(6)
{
    border: unset;
}
.subscription-profile
{
    width: 500px;
    height: 100px;
    border-radius: 30px;
    background-color: white;
    position: relative;
    margin: auto;
    margin-top: 40px;
}
.icon-subscription-profile
{
    width: 45px;
    height: 45px;
    border-radius: 50%;
    background-color: rgb(255, 255, 255);
    top: -21px;
    position: absolute;
    left: 230px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.icon-subscription-profile > div
{
    width: 36px;
    height: 36px;
    border-radius: 50%;
    background-color: #e4e4e4;
    display: flex;
    justify-content: center;
    align-items: center;
}
.icon-subscription-profile> div > svg{
    color:#b6b6b6
}
.row1-subscription-profile
{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.row1-subscription-profile > div
{
    width: 200px;
    height: 65px;
   
}
.row1-subscription-profile > :nth-child(1)
{
    border-left: 1px solid;
    cursor: pointer;
}
.row1-subscription-profile > :nth-child(2)
{
    cursor: pointer;
}

.row1-subscription-profile > :nth-child(1) > svg
{
    color: green;
}
.row1-subscription-profile > :nth-child(1) > p
{
    font-size: 14px;
    color: gray;
}

.row1-subscription-profile > :nth-child(2) > svg
{
    color: green;
}
.row1-subscription-profile > :nth-child(2) > p
{
    font-size: 14px;
    color: gray;
}
.majle-profile:hover > svg
{
    transition: 500ms ease;
    font-size: 17px;
}
.majle-profile:hover > p
{
    transition: 500ms ease;
    font-size: 17px;
}
.dastyar-profile:hover > svg{
    transition: 500ms ease;
    font-size: 17px;
}
.dastyar-profile:hover > p{
    transition: 500ms ease;
    font-size: 17px;
}
.container-logout-profile
{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}
.logout-profile
{
    width: 500px;
    height: 50px;
    border-radius: 30px;
    background-color: white;
    position: relative;
    margin-top: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: 500ms ease;
  
}
.container-logout-profile > :nth-child(2)
{
    width: 500px;
}
.logout-profile > p
{
    color: red;
}
.logout-profile:hover
{
    margin-bottom: 20px;
    transition: 500ms ease;
    margin-top: 30px;
}

/* ------------------------ med ------------------*/

@media (max-width: 535px) {
    .edit-button-info-user-profile
    {
        margin: auto;
        margin-top: 20px;
    }
    .content-profile > :nth-child(1) > div
    {
        width: 100%;
    }
    .info-user-profile
    {
        width: 98%;
       
    }

    .subscription-profile
    {
        width: 98%;
        
    }
}

